import React from 'react';
import { Link } from 'react-router-dom';
import './nav-css/PlacesContainer.css';

const places = ['sea', 'mountain', 'sight', 'village']
let locationValue = ""
export default class SinglePlace extends React.Component {
  constructor(){
    super();
    this.state = {
      nameValue: "",
      locationValue: ""
    };
    this.handleNameChange = this.handleNameChange.bind(this);
    this.handleLocationChange = this.handleLocationChange.bind(this);
  }
  getPlace() {
    switch(this.props.place) {
      case "sea":
        return require('../sea-places/all-sea-places');
      case "mountain":
        return require('../mountain-places/all-mountain-places');
      case "village":
        return require('../village-places/all-village-places');
      case "sight":
        return require('../sight-places/all-sight-places');
      default:
        return ""
    }
  }
  formatValue(value) {
    value = value.toLowerCase();
    return value.replace(value.slice(0,1), value.slice(0,1).toUpperCase());;
  }
  handleNameChange(event) {
    this.setState({
      nameValue: this.formatValue(event.target.value)
    });
  }
  handleLocationChange(event) {
    this.setState({
      locationValue: this.formatValue(event.target.innerHTML)
    });
  }
  filterResults(placeName, placeLocation) {
    console.log(placeLocation, this.state.locationValue);
    if (placeName.toLowerCase().search(this.state.nameValue.toLowerCase()) !== -1) {
      return placeName;
    }
    else {
      return placeName === " "; //return all
    }
  }
  render () {
    let topics = this.getPlace();
    return (
      <div className = "places-container">
         <div className = "search-container">
           <div className="search-specific">
             <h2>:</h2>
             <input className = "search-box" placeholder = " " value = {this.state.nameValue} onChange = {this.handleNameChange.bind(this)}/>
           </div>
           <div className="search-specific-region">
             <h2>:</h2>
             <div className="search-box">
                <span>{this.state.locationValue ? this.state.locationValue : " "}</span>
                <div className="search-regions">
                  <h2 onClick={this.handleLocationChange.bind(this)}></h2>
                  <h2 onClick={this.handleLocationChange.bind(this)}></h2>
                </div>
             </div>
           </div>
        </div>
        {topics.default.filter(({name, region}) => this.filterResults(name, region)).map(({name, id, image, description}) => (
          <ul key = {id}>
          <Link to ={{
            pathname: `/places/${this.props.place}/${id}`,
            state: {
              fromSea: {name, image, description},
            }
          }}>
          <h2>{name}</h2>
          <img key = {image} src = {require(`../${this.props.place}-places/${this.props.place}-img/${image}`)} width = "300" height = "200" alt = ""/>
          </Link>
          </ul>
        ))}
      </div>
    );
  }
};



